@font-face {
  font-family: "icomoon";
  src: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/93/icomoon.eot?3qkin2");
  src: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/93/icomoon.eot?#iefix3qkin2")
      format("embedded-opentype"),
    url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/93/icomoon.woff?3qkin2")
      format("woff"),
    url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/93/icomoon.ttf?3qkin2")
      format("truetype"),
    url("fhttps://s3-us-west-2.amazonaws.com/s.cdpn.io/93/icomoon.svg?3qkin2#icomoon")
      format("svg");
  font-weight: normal;
  font-style: normal;
}

.social-networks {
  margin: -1px 0 -1px;
  padding-bottom: 17px;
  font-size: 0;
  text-align: center;
  background-color: #dc2c2c;
}

.social-networks li {
  display: inline-block;
}

.social-networks a {
  display: block;
  position: relative;
  margin: 0 7px;
  width: 68px;
  height: 68px;
  font-size: 0;
  line-height: 0;
  text-align: center;
  text-decoration: none;
}

.social-networks a:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  margin-top: -50%;
  margin-left: -50%;
  border-radius: 100px;
  background-color: #1f2027;
  transition: all 400ms;
}

.social-networks a:after {
  position: relative;
  display: block;
  font-family: "icomoon";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  color: #fff;
  font-size: 34px;
  line-height: 68px;
}

.social-networks a:hover,
.social-networks a:focus {
  text-decoration: none;
}

.social-networks.spin-icon a:hover:after,
.social-networks.spin-icon a:focus:after {
  transition: -webkit-transform 0.4s ease-out;
  transition: transform 0.4s ease-out;
  transition: transform 0.4s ease-out, -webkit-transform 0.4s ease-out;
  -webkit-transform: rotateZ(360deg);
  transform: rotateZ(360deg);
}

.social-networks.square a:hover:before,
.social-networks.square a:focus:before {
  border-radius: 0;
}

.social-networks.bounce a:hover,
.social-networks.bounce a:focus {
  -webkit-animation: bounce 1s;
  animation: bounce 1s;
}

.social-networks .icon-linkedin:after {
  content: "";
}

.social-networks .icon-linkedin:hover:before,
.social-networks .icon-linkedin:focus:before {
  background: #0077b5;
}

